import React from "react";
import Tilt from 'react-tilt';
import Img from "gatsby-image";
import OnVisible, { setDefaultProps } from 'react-on-visible';

setDefaultProps({
    visibleClassName: 'in-viewport',
    percent: 40
});

const CaseStudy = (props) => (
    <section className="case-study" id="projects">
        <OnVisible className="case-study-text">
            <h3>Lorem ipsum dolor</h3>

            <p>
                A lightweight WordPress website, focusing on speed and performance.
            </p>

            <p>
                <a href="/about">See More</a>
            </p>
        </OnVisible>
        <OnVisible className="case-study-img">
            <Tilt className="Tilt" options={{ max : 20, glare: true, scale: 1}} >
                <Img fluid={props.imgSrc} />
            </Tilt>
        </OnVisible>
    </section>
)

export default CaseStudy