import React from "react"
import { graphql } from "gatsby";
import Particles from 'react-particles-js';
import Layout from "../components/layout"
import Head from "../components/head"
import Intro from "../components/intro"
import AboutMe from "../components/aboutMe"
import CaseStudy from "../components/caseStudy"
import CaseStudyGrid from "../components/caseStudyGrid"

import Telescope from "../assets/images/telescope.jpg"

import("../assets/sass/styles.scss")


const IndexPage = ({ data }) => (
    <Layout>
        <Head />

        <Particles
            width="100%"
            height="100%"
            className="particles"
            params={{
                particles: {
                    number: {
                        value: 92,
                        density: {
                            enable: true,
                            value_area: 150,
                        },
                    },
                    color: {
                        value: "#fff",
                    },
                    line_linked: {
                        enable: false,
                    },
                    move: {
                        direction: "none",
                        speed: 0.3,
                    },
                    size: {
                        value: 2,
                    },
                    opacity: {
                        anim: {
                            enable: true,
                            speed: 1,
                            opacity_min: 0.05,
                        },
                    },
                },
                retina_detect: true,
            }}
        />

        <Intro imgSrc={data.bgImg.childImageSharp.fluid} />

        <AboutMe />

        <CaseStudy imgSrc={data.herminius.childImageSharp.fluid} />

        <section className="masonry-layout">
            <CaseStudyGrid
                title="Title"
                description="A WordPress site for the collaboration of the eight most research intensive Universities in the North of England."
                imgSrc="https://placehold.it/582x200"
                imageAlt="Alt"
                link=""
                hoverState=""
            />
            <CaseStudyGrid
                title="Title"
                description="Sit amet, consectetur adipisicing elit. Aspernatur rerum odit"
                imgSrc="https://placehold.it/582x640"
                imageAlt="Alt"
                link=""
                hoverState=""
            />
            <CaseStudyGrid
                title="Jodrell Bank"
                description="Showcasing The Jodrell Bank Observatory."
                imgSrc={Telescope}
                imageAlt="Alt"
                link="about"
                hoverState="gradient-fade-up"
            />
        </section>
    </Layout>
)

export default IndexPage


export const fluidImage = graphql`
    fragment fluidImage on File {
        childImageSharp {
            fluid(quality: 100) {
                ...GatsbyImageSharpFluid_withWebp
            }
        }
    }
`;

export const pageQuery = graphql`
    query {
        bgImg: file(relativePath: { eq: "bg.jpg" }) {
        ...fluidImage
        }
        herminius: file(relativePath: { eq: "herminius.jpg" }) {
        ...fluidImage
        }
    }
`;