import React from "react";
import OnVisible, { setDefaultProps } from 'react-on-visible';

setDefaultProps({
    visibleClassName: 'in-viewport',
    percent: 40
});

const CaseStudyGrid = (props) => {
    const url = props.link;

    return (
        <OnVisible className={ `panel ${ props.hoverState }` }>

            <img src={props.imgSrc} alt={props.imageAlt} />

            <div className="content">
                <h3>{props.title}</h3>

                <p>
                    {props.description}
                </p>

                {url.length > 0 &&
                    <button><a href={props.link}>Read More</a></button>
                }
            </div>

        </OnVisible>
    )
}

export default CaseStudyGrid